button {
	outline: none;
	border: none;
}

header#header {
  background: transparent;
  padding: 0 !important;
	height: 90px;
	display: flex;
	align-items: center;
	justify-content: center;
	position: absolute;
	// top: 0;
	// left: 0;
	width: 100%;
}

.fixed-header {
	position: fixed !important;
	height: 60px !important;
	top: -60px;
	left: 0;
	right: 0;
	z-index: 999;
	background-color: #111 !important;

	.logo {
		img {
			height: 35px;
		}
	}

	.header-progress {
		height: 2px;
		background-color: #fff;
		position: absolute;
		bottom: 0;
		left: 0;
	}
}

.nav-holder {
	max-width: 92% !important;
	position: relative;
}

.logo {
  z-index: 50;
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	left: 0;
	transform: translateY(-50%);
	top: 50%;

	img {
		height: 35px;
	}
}

.absolute {
	position: absolute !important;
}

nav.navbar {
  padding: 0;
}

nav.navbar .nav-container {
	width: 100% !important;
  display: flex;
  justify-content: space-between;
}

.left-part {
	display: flex;
	// width: 50%;
	justify-content: space-between;
	z-index: 100;

	.shop-cart {
		display: none;
		margin-right: 12px;
	}

	.navbar-toggler {
		display: none;
	}
}

.right-part {
	// width: 30%;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	z-index: 100;

	.navbar-toggler {
		// display: none;
		margin-left: 2.5rem !important;
		padding: 0.35rem 0 0 0.75rem !important;
	}
}

.big-screen-menu {
	color: #F5F6F7;
	cursor: pointer;
	font-size: 12px;
	font-weight: 600;
	text-transform: uppercase;
	transition: 0.3s linear;
	display: flex;
	align-items: center;

	span.menu--icon {
		position: relative;
    width: 50px;
    height: 50px;
    line-height: 48px;
    border-radius: 50%;
    text-align: center;
    border: 1px solid #918f8f;
    display: flex;
		justify-content: center;
		align-items: center;
    margin-right: 20px;
    -webkit-transition: 0.3s linear;
    -moz-transition: 0.3s linear;
    -ms-transition: 0.3s linear;
    -o-transition: 0.3s linear;
    transition: 0.3s linear;

		span {
			display: inline-block;
			width: 5px;
			height: 5px;
			border-radius: 50%;
			background-color: #F5F6F7;
			margin: 0 3px;
			-webkit-transition: 0.3s linear;
			-moz-transition: 0.3s linear;
			-ms-transition: 0.3s linear;
			-o-transition: 0.3s linear;
			transition: 0.3s linear;
		}
	}

	&:hover {
		color: #fff;

		span.menu--icon {
			border-color: #fff;

			span {
				background-color: #fff;
			}
		}
	}

	// svg {
	// 	width: 40px;
	// 	height: 40px;
	// 	margin-right: 10px;
	// }

}

.right-part .nav-search {
	position: relative;
}

.nav-container .shop-cart {
	position: relative;
}

.right-part .account {

	>a {
		color: #F5F6F7;
		cursor: pointer;
		font-size: 19px;
    font-weight: 500;
    height: 28px;
	
		&:hover {
			color: #f26c4f;
		}

	}
}

.right-part .search-icon {
	width: 28px;
	height: 28px;
	color: #F5F6F7;
	flex-shrink: 0;
	cursor: pointer;
	transition: all 0.3s ease;

	&:hover {
		color: #F26C4F;
		
	}
}

.right-part span {
	font-size: 22px;
	margin: 0 1.05rem;
	color: #F5F6F7;
	text-align: center;
}

.nav-container .shop-cart  {
	cursor: pointer;

	.shopping-bag-icon {
		width: 30px;
		height: 29px;
		color: #F5F6F7;
		flex-shrink: 0;
		cursor: pointer;
		transition: all 0.3s ease;
	}

	label.cart-module-label {
		background-color: #f26c4f;
		width: 19px;
		height: 19px;
		line-height: 19px;
		border-radius: 50%;
		color: white;
		font-size: 11px;
		font-weight: 400;
		text-align: center;
		position: absolute;
		top: 65%;
		left: 50%;
		display: inline-block;
		cursor: pointer;
	}

	&:hover {
		.shopping-bag-icon {
			color: #F26C4F;
		}
		
	}
}

.navbar-toggler {
  color: #F5F6F7;
  z-index: 10;
	border: none !important;
	margin: 0 !important;
	padding-right: 0 !important;

	svg path {
		stroke: #F5F6F7;
	}
}

.nvabar-links .navbar-ul {
  display: flex;
  align-items: center;
	margin: 0;
}

.nav-link {
  color: #707070;
	font-family: "Montserrat", sans-serif;
	font-size: 15px;
	text-transform: capitalize;
	font-weight: 500;
}

.nav-link:hover {
  color: #606da6;
}

.nav-link.active {
  color: #606da6;
}

.burger-nav-menu {
	transition: all 0.3s ease-in-out;
	color: #F5F6F7 !important;
	
	&:hover {
		color: #fff !important;
	}
} 

.mobile-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
	z-index: 19;
  display: flex;
  justify-content: center;
  align-items: center;
	// pointer-events: none;

	&.hideMobileNav {
		pointer-events: none;
	}
	
	.menubg {
		pointer-events: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		bottom: 0;
		background: rgba(17, 17, 17, 1);
	}

	.navbar-ul {
		padding: 0;
		margin: 0;

		.nav-item {
			padding: 0;
			margin: 0;
		}

		.nav-item.search-item {
			display: none;
		}

		.nav-item.account {
			display: none;
		}

		.nav-link {
			font-family: "Montserrat", sans-serif;
			font-size: 30px;
			color: white;
			font-weight: 700;
			line-height: 58px;
			text-transform: capitalize;
			display: block;
			text-align: center;
			transition: 0.3s all;
		}

		.nav-link:hover {
			color: #606da6;
		}

		.nav-link.active {
			color: #606da6;
		}

	}

}

.d-none {
  display: none;
}

