.show-mobile-nav {
	display: block !important;
	/* animation: showMobileNav 0.4s ease forwards; */
}

.mobile-nav .navbar-ul {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
	z-index: 20;
}

.mobile-nav .navbar-ul li {
  margin: 25px 0;
}

.account {
	position: relative;
	height: 100%;
	display: flex;
	align-items: center;
	
	.acc-dropdown-btn {
		transition: all 0.3s ease;
		cursor: pointer;
		font-size: 18px;

		svg {
			margin-left: 4px;
			width: 20px;
			height: 20px;
		}

		&:hover {
			color: #f26c4f;
		}
	}

	.account-dropdown {
		position: absolute;
		top: 50px;
		right: 0;
		display: none;
		border-radius: 12px;
		background-color: #161515;
		overflow: hidden;
		width: 140px !important;
	
		&.active {
			display: block;
		}

		.user-profile,.user-logout,.user-wishlist {
			padding: 9px 15px;
			color: #f1eeee;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			font-size: 16px;
			cursor: pointer;
			z-index: 50 !important;
			position: relative;

			svg {
				margin-right: 10px;
				width: 18px;
				height: 18px;
			}

			&:hover {
				color: #fff;
				background-color: #292727;
			}
		}

	}
}

@keyframes showMobileNav {
  
  0% {
    transform: translateY(-100vh);
  }

  100% {
    transform: translateY(0);
  }
}

@keyframes hideMobileNav {
  
  0% {
    transform: translateY(0);
  }

  100% {
    transform: translateY(-100vh);
  }
}

