@media( max-width: 990px ){
	.nav-holder {
		max-width: 720px !important;
	}
}

@media( max-width: 768px ){

	.logo {
		img {
			height: 40px;
		}
	}

	.nav-holder {
		max-width: 540px !important;
	}

	.nav-container .shop-cart {
		transform: translateY(-2px);
		margin-right: 15px;
	}

	.left-part {

		.shop-cart {
			display: block;
		}

		.navbar-toggler {
			display: block;
		}
	}

	.nav-container {
		flex-direction: row-reverse;

		.logo {
			left: 0;
			transform: translateX(0%) translateY(-50%);
		}
	}

	.mobile-nav .navbar-ul {
		margin-top: 70px;
	}

	.burger-nav-menu {
		margin-left: 0;
	}

	.right-part .shop-cart {
		margin-right: 0px;
	}

	.right-part {
		display: none;
	}

	// .mobile-nav {
		
