/*------------------------------------*\
    #BUTTONS
\*------------------------------------*/

.btn {
    font-family: $font-heading;
    position: relative;
    z-index: 2;
    font-size: 14px;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    border-radius: 0;
    padding: 0 20px;
    line-height: 43px;
    width: max-content;
    min-width: 170px;
    white-space: nowrap;
    height: 45px;
    letter-spacing: 1px;
    border: none;
    color: $color-black;
}

.btn.active,
.btn:active {
     @include prefix(box-shadow, none, moz webkit);
}
 
/* Button Primary */

.btn--primary {
    background-color: $color-theme;
    color: #f5f5f5;
    border: 2px solid transparent;
}

.btn--primary:active,
.btn--primary:focus,
.btn--primary:hover {
    background-color: $color-dark;
    border: 2px solid $color-dark;
    color: $color-white;
}

/* Button Secondary*/
.btn--secondary {
    background-color: $color-dark;
    color: $color-white;
    border: 2px solid $color-dark;
}

.btn--secondary:active,
.btn--secondary:focus,
.btn--secondary:hover {
    background-color: $color-theme;
    color: $color-white;
    border-color: $color-theme;
}

/* Button White */

.btn--white {
    background-color: $color-white;
    color: $color-heading;
    border: 2px solid $color-white;
}

.btn--white:active,
.btn--white:focus,
.btn--white:hover {
    background-color: $color-theme;
    color: $color-white;
    border-color: $color-theme;
}

.bg-overlay-theme2 .btn--white:active,
.bg-overlay-theme2 .btn--white:focus,
.bg-overlay-theme2 .btn--white:hover {
    background-color: $color-theme;
    color: $color-white;
    border-color: $color-theme;
}


/* Button Block */

.btn--block {
    width: 100%;
}

.btn--auto {
    width: auto;
}

/* Button Rounded */

.btn--rounded {
    border-radius: 50px;
}

/* Button Link */

.btn--link {
    background-color: transparent;
    border-color: transparent;
}

.btn--link.btn--primary {
    color: $color-theme;
}

.btn--link.btn--primary:active,
.btn--link.btn--primary:focus,
.btn--link.btn--primary:hover {
    color: $color-dark;
    background-color: transparent;
    border-color: transparent;
}

.btn--link.btn--secondary {
    color: $color-dark;
}

.btn--link.btn--secondary:active,
.btn--link.btn--secondary:focus,
.btn--link.btn--secondary:hover {
    color: $color-theme;
    background-color: transparent;
    border-color: transparent;
}

/* Button Bordered */

.btn--bordered {
    background-color: transparent;
}

.btn--bordered.btn--primary {
    color: $color-theme;
    border-color: $color-theme
}

.btn--bordered.btn--primary:active,
.btn--bordered.btn--primary:focus,
.btn--bordered.btn--primary:hover {
    color: $color-white;
    background-color: $color-theme;
    border-color: $color-theme
}

.btn--bordered.btn--secondary {
    color: $color-dark;
    border-color: $color-dark
}

.btn--bordered.btn--secondary:active,
.btn--bordered.btn--secondary:focus,
.btn--bordered.btn--secondary:hover {
    color: $color-white;
    background-color: $color-dark;
    border-color: $color-dark
}

.btn--bordered.btn--white {
    color: $color-white;
    border-color: $color-white;
    background-color: transparent;
}

.btn--bordered.btn--white:active,
.btn--bordered.btn--white:focus,
.btn--bordered.btn--white:hover {
    color: $color-theme;
    background-color: $color-white;
    border-color: $color-white
}

.btn--white.btn--secondary {
    color: $color-dark;
    border-color: $color-white;
    background-color: $color-white;
}

.btn--white.btn--secondary:active,
.btn--white.btn--secondary:focus,
.btn--white.btn--secondary:hover {
    color: $color-white;
    background-color: $color-theme;
    border-color: $color-theme;
}

.btn--slide:last-of-type {
    margin-left: -6px;
}

.btn--slide-wide {
    width: 270px;
}

.btn--video {
    color: $color-white;
    font-size: 13px;
    font-weight: 600;
    line-height: 51px;
    text-transform: uppercase;
    padding-left: 93px;
}

.btn--video i {
    position: absolute;
    left: 30px;
    top: 0;
    color: $color-gray;
    font-size: 48px;
}

.btn--video:hover,
.btn--video:hover i {
    color: $color-theme
}

/* Buttom Video With Bordered */

.btn--video-bordered {
    width: 68px;
    height: 68px;
    line-height: 65px;
    border-radius: 50%;
    border-radius: 33px;
    background-color: rgba(white, 0.2);
    @include transition(all .4s);
}

.btn--video-bordered .btn--video {
    padding-left: 0;
    width: 48px;
    height: 48px;
    color: #f9f9f9;
}

.btn--video-bordered .btn--video i {
    left: 9px;
}

.btn--video-bordered:hover {
    background-color: $color-white;
}

.btn--video-bordered:hover .btn--video,
.btn--video-bordered:hover .btn--video i {
    color: $color-theme
}

/* Buttom More */

.btn-more span {
    position: relative;
    padding-right: 7px
}

.btn-more span:before {
    position: absolute;
    content: "";
    left: 0;
    top: 50%;
    width: 8px;
    margin-left: -7px;
    height: 2px;
    background-color: #222222;
}

.btn-more span:after {
    position: absolute;
    content: "";
    left: -4px;
    top: 50%;
    margin-top: -3px;
    width: 2px;
    height: 8px;
    background-color: #222222;
}

.btn-more:hover span:after,
.btn-more:hover span:before {
    background-color: $color-white
}

/* Buttom Link */

.btn--link{
    &.btn-more {
        color: $color-theme;
        height: 15px;
        line-height: 15px;
        
        span{
            &:after,
            &:before {
            background-color: $color-theme
            }
        }

        &:hover {
            color: $color-heading;
            span{
                &:after,
                &:before {
                    background-color: $color-heading
                }
            }
        }
    }
}

/* Button Action */

.btn--action {
    line-height: 130px;
    height: 130px;
    span{
        &:after,
        &:before {
        background-color: $color-white
        }
    }
}

/* Button Icon */

.btn--icon {
    width: 70px;
    height: 70px;
    line-height: 70px;
    text-align: center;
    border-radius: 2px;
    background-color: rgba(27, 26, 26, 0.9);
    font-size: 40px;
    display: inline-block;
    @include transition( all .4s linear);
}

.btn--hover-white:active,
.btn--hover-white:focus,
.btn--hover-white:hover {
    background-color: $color-white;
    color: $color-heading;
    border-color: $color-white
}

.btn--white {
    background-color: $color-white;
    width: 216px;
    height: 50px;
    font-family: $font-body;
    font-size: 17px;
    font-weight: 700;
    text-transform: capitalize;
    color: #2b2e34;
    @include prefix(box-shadow, 0px 6px 12px 0px rgba(40, 43, 49, 0.08), moz webkit);
    border-color: transparent;
}

.btn--link {
    font-family: $font-body;
    font-size: 14px;
    font-weight: 400;
    text-transform: capitalize;
    color: #469af4;
    text-decoration: underline;
    height: auto;
    i {
        margin-left: 8px;
    }
    
}

.btn--block {
    width: 100%!important;
}

.btn--more {
    font-size: 12px;
    color: $color-body;
    font-family: $font-heading;
    text-transform: uppercase;
    line-height: 1;
    font-weight: 700;
    position: relative;
    padding-top: 80px;
    display: inline-block;
    letter-spacing: 3px;
    &:after {
        content: "";
        position: absolute;
        left: 50%;
        top: 0;
        width: 1px;
        height: 40px;
        background-color: #cccccc;
        @include transition(all .4s linear);
    }

    &:hover {
        color: $color-theme;
        &:after {
            background-color: $color-theme;
        }
    }
    
}

.btn--browse-cat {
    font-family: $font-body;
    color: $color-black;
    text-transform: capitalize;
    font-size: 16px;
    width: auto;
    height: auto;
    line-height:72px;
    i {
        margin-left: 35px;
        color: $color-theme;
    }
    &:hover{
        color: $color-theme;
    }
}

/* Custom, iPhone Retina */ 
@media only screen and (min-width : 320px) and (max-width : 767px) {
    .btn--browse-cat {
        line-height:42px;
    }
}
